<template>
<div>
<div class="wrap">
      <div class="picContent">
          <div class="image scale-1px">
              <img v-if="!getImg" src="../../../static/img/picLogo.png">
               <img v-else :src="getImg"> 
          </div>

          <div class="Btn">
              <div class="choose scale-1px">
                从相册中拍一张
                <input @change="getfile($event)" type="file" class="input_File"  accept="image/*" >
              </div>
              <div class="take scale-1px">
                拍一张照片
              <input @change="getfile($event)" type="file" class="input_File" accept="image/*" capture="camera">
              </div>
              
     


         </div>

      </div>

</div>
  <m-footer>
    <span slot="item3">在线咨询</span>
    <span slot="item4">预约看牙</span>
  </m-footer>

</div>

</template>

<script type="text/ecmascript-6">
 import { Toast } from 'mint-ui'
 import footer from "@/components/base/footer"
 export default {
   data(){
    return{
     getImg:'',
    }
  },
  components: {
      MFooter:footer
   },
  created(){

},
methods:{
    getfile(event){
      let file=event.target.files[0];
      let fileName=file.name
      let _this= this
      let suffix=fileName.substring(fileName.lastIndexOf(".")+1).toLowerCase()
      if(suffix !="jpg" && suffix !="jpeg" && suffix !="png" && suffix !="git" && suffix !="bmp" && suffix !="pdf"){
          Toast('请选择图片格式为jpg,png,git.pdf,bmp的上传')
          event.target.value=""
         return 
      }else{
         let reader = new FileReader();
          reader.readAsDataURL(file);
          reader.onload = function() {
          let image = new Image()
          image.src=reader.result
          image.onload=function(){
                _this.getImg=reader.result
          }   
        }

      } 

/*     let self =this
      this.$http.post(this.GLOBAL.BASE_URL+'/api/Information/user_img_update', ({   
             image:this.getImg
          　　//这里是传参数
          })).then(function (response) {
          //console.log(response)
          console.log(response.data.data)
           Toast('上传头像成功')

          setTimeout(() => {
            self.$router.push('/UserList')
           }, 4000)

          }).catch(function (error) {
          　　console.log(error);
          }); 
*/

     },       
   }

}
 
</script>

<style lang="stylus" type="text/stylus" scoped>  
/*修改头像部分*/
.picContent
  width:6.4rem
  height:100%
.image
  margin:auto
  margin-top:10%
  width:3.5rem
  height:3.5rem
  border-radius:50% 
  overflow:hidden 
  img
    width:100%
    height:100%
    display:block
    margin-bottom:1rem
   
.Btn
  margin:auto 
  width:3.8rem
  margin-top:0.8rem
.choose,.take
   margin:15px 0
   width:100%
   height:0.85rem
   line-height:0.85rem
   text-align:center
   //border:1px solid rgba(231,227,227,0.3) 
   //box-shadow: 0px 2px  4px rgba(231,227,227,0.5)
   color:#666
   font-size:0.28rem
   position:relative
   .input_File
      color:red
      width:3.5rem
      height:0.8rem
      overflow:hidden
      background:red
      position: absolute
      box-sizing:content-box
      z-index:10
      font-size:1rem
      opacity:0
      top:0
      left:0
      right:0
      bottom:0
.back
   margin:15px 0
   width:3.5rem
   height:0.8rem
   line-height:0.8rem
   font-size:0.28rem
   text-align:center



 	
 	
</style>



